'use client'

import { useState, useEffect } from 'react'
import ProductList from '@/components/ProductList'

// Mock products data - in a real app, this would come from an API
const mockProducts = [
  {
    id: 1,
    name: 'Premium Cotton T-Shirt',
    price: 89,
    image: 'https://images.unsplash.com/photo-1521572163474-6864f9cf17ab?w=400&h=400&fit=crop',
    category: 'T-Shirts',
    description: 'Soft, breathable cotton t-shirt perfect for everyday wear.'
  },
  {
    id: 2,
    name: 'Classic Denim Jacket',
    price: 199,
    image: 'https://images.unsplash.com/photo-1544022613-e87ca75a784a?w=400&h=400&fit=crop',
    category: 'Jackets',
    description: 'Timeless denim jacket with a modern fit and premium construction.'
  },
  {
    id: 3,
    name: 'Luxury Wool Sweater',
    price: 159,
    image: 'https://images.unsplash.com/photo-1434389677669-e08b4cac3105?w=400&h=400&fit=crop',
    category: 'Sweaters',
    description: 'Cozy wool sweater crafted from the finest materials.'
  },
  {
    id: 4,
    name: 'Slim Fit Chinos',
    price: 129,
    image: 'https://images.unsplash.com/photo-1473966968600-fa801b869a1a?w=400&h=400&fit=crop',
    category: 'Pants',
    description: 'Versatile chinos with a modern slim fit and comfortable stretch.'
  },
  {
    id: 5,
    name: 'Casual Button Shirt',
    price: 109,
    image: 'https://images.unsplash.com/photo-1596755094514-f87e34085b2c?w=400&h=400&fit=crop',
    category: 'Shirts',
    description: 'Crisp button-up shirt perfect for both casual and formal occasions.'
  },
  {
    id: 6,
    name: 'Hooded Sweatshirt',
    price: 139,
    image: 'https://images.unsplash.com/photo-1556821840-3a63f95609a7?w=400&h=400&fit=crop',
    category: 'Hoodies',
    description: 'Comfortable hooded sweatshirt with premium fleece lining.'
  }
]

const categories = ['All', 'T-Shirts', 'Jackets', 'Sweaters', 'Pants', 'Shirts', 'Hoodies']

export default function ShopPage() {
  const [products, setProducts] = useState(mockProducts)
  const [filteredProducts, setFilteredProducts] = useState(mockProducts)
  const [selectedCategory, setSelectedCategory] = useState('All')
  const [sortBy, setSortBy] = useState('name')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  useEffect(() => {
    filterAndSortProducts()
  }, [selectedCategory, sortBy, products])

  const filterAndSortProducts = () => {
    let filtered = products

    // Filter by category
    if (selectedCategory !== 'All') {
      filtered = products.filter(product => product.category === selectedCategory)
    }

    // Sort products
    filtered = [...filtered].sort((a, b) => {
      switch (sortBy) {
        case 'price-low':
          return a.price - b.price
        case 'price-high':
          return b.price - a.price
        case 'name':
        default:
          return a.name.localeCompare(b.name)
      }
    })

    setFilteredProducts(filtered)
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-red-600 mb-4">Error Loading Products</h2>
          <p className="text-gray-600 mb-4">{error}</p>
          <button 
            onClick={() => window.location.reload()}
            className="bg-black text-white px-6 py-2 font-semibold hover:bg-gray-800 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl md:text-4xl font-bold text-black mb-4">Shop Collection</h1>
          <p className="text-lg text-gray-600">
            Discover our complete range of premium clothing
          </p>
        </div>

        {/* Filters */}
        <div className="bg-white p-6 rounded-lg shadow-sm mb-8">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
            {/* Category Filter */}
            <div className="flex flex-wrap gap-2">
              {categories.map((category) => (
                <button
                  key={category}
                  onClick={() => setSelectedCategory(category)}
                  className={`px-4 py-2 text-sm font-medium transition-colors ${
                    selectedCategory === category
                      ? 'bg-black text-white'
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  {category}
                </button>
              ))}
            </div>

            {/* Sort Filter */}
            <div className="flex items-center gap-2">
              <label htmlFor="sort" className="text-sm font-medium text-gray-700">
                Sort by:
              </label>
              <select
                id="sort"
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                className="border border-gray-300 rounded px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-black"
              >
                <option value="name">Name</option>
                <option value="price-low">Price: Low to High</option>
                <option value="price-high">Price: High to Low</option>
              </select>
            </div>
          </div>
        </div>

        {/* Products Count */}
        <div className="mb-6">
          <p className="text-gray-600">
            Showing {filteredProducts.length} of {products.length} products
          </p>
        </div>

        {/* Products Grid */}
        {loading ? (
          <div className="flex justify-center items-center py-12">
            <div className="text-lg text-gray-600">Loading products...</div>
          </div>
        ) : filteredProducts.length === 0 ? (
          <div className="text-center py-12">
            <h3 className="text-xl font-semibold text-gray-700 mb-2">No products found</h3>
            <p className="text-gray-600">Try adjusting your filters or browse all categories.</p>
          </div>
        ) : (
          <ProductList products={filteredProducts} />
        )}
      </div>
    </div>
  )
}
